import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'customer'
})
export class CustomerPipe {
  transform(value) {
    let result :string;
    switch(value){
        case 'COMPLETEPROJECT':
            result = '';
            break;
        case 'PROJECTING':
            result = ''
            break;
        case 'POTENTIAL':
            result = ''
            break;
        case 'VIP':
            result = 'VIP';
            break;
        case 'OLD':
            result = ''
            break;
        case 'COOPERATOR':
            result = ''
            break;
        case 'ORDINARY':
            result = ''
            break;
        }
    return result;
  }
}


