import { Pipe, PipeTransform } from '@angular/core';


@Pipe({
  name: 'color',
})
export class ColorPipe implements PipeTransform {
  
  transform(value: string, ...args) {
    let result :string;
    switch(value){
        case 'WAITAUDIT'://
          result = 'orange'
          break;
        case 'WAITPAY'://
          result = 'red'
          break;
        case 'WAITRETURN'://
          result = 'green'
          break;
        case 'HASRETURN'://
          result = 'blue'
          break;
        case 'HASLEND'://
          result = 'Gcolor'
          break;
        case 'WAITTHAW'://
          result = 'grey'
          break;
        //
        case 'WAITANALISIS'://
          result = 'red'
          break;
        case 'WAITCHECK'://
          result = 'green'
          break;
        case 'HASREIM'://
          result = 'blue'
          break;
      }
    return result;
  }
}



