import { Pipe } from '@angular/core';

@Pipe({
  name: 'status'
})
export class StatusPipe {
  transform(value) {
    let result :string;
    switch(value){
        case 'WAITAUDIT'://
            result = '';
            break;
        case 'WAITPAY'://
            result = ''
            break;
        case 'WAITRETURN'://
            result = ''
            break;
        case 'HASRETURN'://
            result = ''
            break;
        case 'HASLEND'://
            result = ''
            break;
        case 'WAITTHAW'://
            result = ''
            break;
        case 'WAITANALISIS':
            result = ''
            break;
        case 'WAITCHECK':
            result = ''
            break;
        case 'HASREIM':
            result = ''
            break;
        case 'ALL':
            result = ''
            break;
        default :
            result = value;
            break;
        }
    return result;
  }
}



